import services from '../services/index';

export default async function getUsers(req, res, next) {
  try {
    const {
      divisionId,
      search,
      limit,
      offset,
      role,
      userType,
      all,
    } = req.query;
    return res.json({
      count: await services.getUsersCount(
        divisionId,
        search,
        limit,
        offset,
        role,
        userType,
        all
      ),
      users: await services.getUsers(
        divisionId,
        search,
        limit,
        offset,
        role,
        userType,
        all
      ),
    });
  } catch (err) {
    return next(err);
  }
}
